import Link from 'next/link';
import NavStyles from './styles/NavStyles';
import User from './User';

const Nav = () => (
  
    <User>
      {({ data: { me } })=> (
        <NavStyles>
        <Link href="/items">
        <a>Shop</a>
      </Link>
      { me && (
        <div>
        
        </div>
        
      )}
      {!me && (
        <Link href="/signup">
        <a>Sign In</a>
      </Link>
      )}
      
      
      
    </NavStyles>
      )}
    </User>
    
);

export default Nav;